import React, { useCallback, useMemo } from 'react';
import {
  Box, Button, FormControl, FormLabel, Input,
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import MiniBox from '../../../../layout/MiniBox';
import { usePersonState } from '../../../../hooks/user';
import AddressSearch from '../../../../components/AddressSearch';
import { useAppAsyncDispatch } from '../../../../hooks/store';
import { updatePerson as updatePersonApi } from '../../../../store/middlewares/user';
import { apiErrorHandler } from '../../../../utils/general';

/**
 * Peroson address screen
 *
 * @component
 * @module KYC
 * @link {@see https://www.figma.com/file/ndxr4b9PADQG9FMkIWp2ie/For-Solid-(Product)?node-id=22252%3A122672}
 * @returns {JSX.Element}
 */
const PersonAddress:React.FC = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const navigate = useNavigate();

  const [personDetails, updatePerson] = usePersonState();

  /**
   * validation memo
   */
  const isValid = useMemo(() => (
    personDetails
      && !!personDetails.address
      && !!personDetails.address.line1
      && !!personDetails.address.city
      && !!personDetails.address.state
      && !!personDetails.address.country
      && !!personDetails.address.postalCode
      && personDetails.address.line1.length !== 0
      && personDetails.address.city.length !== 0
      && personDetails.address.state.length !== 0
      && personDetails.address.country.length !== 0
      && personDetails.address.postalCode.length !== 0
  ), [personDetails]);

  /**
   * Submit callback - onsuccess navigate to idv verification screen
   */
  const submit = useCallback(() => {
    if (isValid && personDetails) {
      const person = {
        address: personDetails.address
      };
      /**
       * Api call
       */
      dispatch(updatePersonApi(personDetails.id, person))
        .then(() => {
          navigate('/ao/kyc/idv');
        }).catch(apiErrorHandler);
    }
  }, [isValid, personDetails]);

  return (
    <div className="page">
      <form onSubmit={(e) => {
        e.preventDefault();
        submit();
      }}
      >
        <MiniBox
          varient="box"
          heading={t('person_address.title')}
          footer={(
            <Button data-testid="submit-btn" type="submit" width="100%" variant="primary" disabled={!isValid}>
              {t('person_address.ctaText')}
            </Button>
          )}
        >
          <Box width="100%" paddingTop="15px">

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('person_address.address1')}
              </FormLabel>
              <AddressSearch
                googleApiKey={process.env.REACT_APP_GMAP_KEY || ''}
                address={personDetails?.address || undefined}
                placeholder={t('person_address.address1')}
                onChange={(address) => {
                  updatePerson('address', {
                    ...address,
                    addressType: 'mailing',
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('person_address.address2')}
              </FormLabel>
              <Input
                variant="primary"
                data-testid="address-line2"
                value={personDetails?.address?.line2}
                placeholder={t('person_address.address2')}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  updatePerson('address', {
                    ...personDetails?.address || {},
                    line2: e.target.value
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('person_address.addressCity')}
              </FormLabel>
              <Input
                variant="primary"
                data-testid="address-city"
                value={personDetails?.address?.city}
                placeholder={t('person_address.addressCity')}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  updatePerson('address', {
                    ...personDetails?.address || {},
                    city: e.target.value
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('person_address.addressState')}
              </FormLabel>
              <Input
                variant="primary"
                data-testid="address-state"
                placeholder={t('person_address.addressState')}
                value={personDetails?.address?.state}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  updatePerson('address', {
                    ...personDetails?.address || {},
                    state: e.target.value
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('person_address.addressZipcode')}
              </FormLabel>
              <Input
                variant="primary"
                data-testid="address-zipcode"
                placeholder={t('person_address.addressZipcode')}
                value={personDetails?.address?.postalCode}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  updatePerson('address', {
                    ...personDetails?.address || {},
                    postalCode: e.target.value
                  });
                }}
              />
            </FormControl>

          </Box>
        </MiniBox>
      </form>
    </div>
  );
};

export default PersonAddress;
