import { shallowEqual, useDispatch } from 'react-redux';
import { updatePersonDetails } from '../store/actionCreators/user';
import { personDetails } from '../types/user';
import { useAppSelector } from './store';

type ValueOf<T> = T[keyof T];

/**
 * Person details hook - it's like useState get and set available
 * @returns {personDetails}
 */
// eslint-disable-next-line import/prefer-default-export
export const usePersonState = (): [
  personDetails | null,
  (
    // eslint-disable-next-line no-unused-vars
    d: personDetails | keyof personDetails,
    // eslint-disable-next-line no-unused-vars
    value?: ValueOf<personDetails>,
  ) => void,
] => {
  const dispatch = useDispatch();
  const personDetailsData = useAppSelector(
    (state) => state.user.personDetails,
    shallowEqual,
  );
  /**
   * update person details state
   * @param {personDetails | string} newData new person details state or key name (update by key case)
   * @param {personDetails=} value valueof personDetails (only for update by key case)
   * @returns {void}
   */
  const updateDetails = (
    newData: personDetails | keyof personDetails,
    value?: ValueOf<personDetails>,
  ) => {
    if (typeof newData === 'string') {
      const key = newData;
      const v = value;
      if (
        key
        && personDetailsData
        && personDetailsData[key] !== undefined
        && v !== undefined
      ) {
        dispatch(updatePersonDetails({ ...personDetailsData, [key]: v }));
      }
    } else {
      dispatch(updatePersonDetails(newData));
    }
  };

  return [personDetailsData, updateDetails];
};
